import React, { Component } from 'react'
// import items from './data'
import client from './Contentful'



const RoomContext = React.createContext();

 class RoomProvider extends Component {
     state={
        rooms:[],
        sortedRooms:[],
        featuredRoom:[],
        loading: true,
        type: "all",
        capacity: 1,
        price: 0,
        minPrice: 0,
        maxPrice: 0,
        minSize: 0,
        maxSize: 0,
        breakfast: false,
        pets: false,
        gallary:[]

     }

     //get data
     getData = async () => {
         try {
             let response = await  client.getEntries({
                 content_type: 'phenixBeachResortRoom',
                 order:'fields.price'
                });
            
            let gallaryItems = await client.getEntries({
                content_type: 'resortGallary'
            })
                let rooms = this.formatData(response.items)
                let gallary = this.formatDatas(gallaryItems.items)
                // this.formatDatas(gallaryItems.items)
        let featuredRooms = rooms.filter(room => room.featured === true);

        let maxPrice = Math.max(...rooms.map(item => item.price));

        let maxSize = Math.max(...rooms.map(item => item.size));

        
        
        this.setState({
            rooms,
            featuredRooms,
            sortedRooms: rooms, 
            loading: false,
            price: maxPrice,
            maxPrice,
            maxSize,
            gallary
            
        })
       
         }catch (error) {
             console.log(error);
         }

     }

     componentDidMount() {
         this.getData();
        
     }

     formatDatas(items) {
         let temp= items && items.map( item => {
             let gallaryimage = item.fields.gallaryImage.fields.file.url;
            let id = item.sys.id;
            let gallaryImg = { id, gallaryimage};
            return gallaryImg
         });
         return temp;
     }

     formatData(items) {
         let tempItems = items.map(item => {
             let id = item.sys.id;
             let images = item.fields.images.map(image => 
                 image.fields.file.url);
             let room= {...item.fields, images, id}
             return room;
         });
        
         return tempItems;
     }

     getRoom = (slug) => {
         let tempRooms = [...this.state.rooms];
         const room = tempRooms.find( room => room.slug === slug);
         return room;
     }

     handleChange = event => {
         const target= event.target;
         const name = event.target.name;
         const value = target.type === 'checkbox' ? target.checked : target.value;
         this.setState({
             [name]: value
         }, this.filterRooms)
     }

     filterRooms = () => {
         let{
             rooms,type,capacity,price,minSize, maxSize, breakfast, pets
         } = this.state;

         //all the rooms
         let tempRooms = [...rooms];

         //transform value from string to integer
         capacity = parseInt(capacity)
         price = parseInt(price)

         //filter by type
         if(type !== 'all'){
             tempRooms = tempRooms.filter(room=> room.type === type)
         }

         //filter by capacity
         if(type !== 1){
            tempRooms = tempRooms.filter(room=> room.capacity >= capacity)
        }

        //filter price
         tempRooms = tempRooms.filter(room=> room.price <= price)

        //filter by size
        tempRooms = tempRooms.filter(room=> room.size >= minSize && room.size <= maxSize)

        //filter by breakfast
        if (breakfast) {
            tempRooms = tempRooms.filter(room => room.breakfast === true );
        }

        // filter by pet
        if (pets) {
            tempRooms = tempRooms.filter(room => room.pets === true );
        }


        // change state
         this.setState({
             sortedRooms: tempRooms
         })
     }

    render() {
        
        return (
            <RoomContext.Provider 
            value={{
                ...this.state,
                getRoom: this.getRoom,
                handleChange: this.handleChange
                }}>
                {this.props.children}
            </RoomContext.Provider>
        )
    }
}

const RoomConsumer = RoomContext.Consumer;

export function withRoomConsumer(Component){
    return function ConsumerWrapper(props){
        return <RoomConsumer>
            {value=> <Component {...props} context={value} />}
        </RoomConsumer>
    }
}

export {RoomProvider, RoomConsumer, RoomContext};